.container {
  width: 100%;
  height: 100%;
  border-radius: inherit;
  overflow: hidden;
  padding: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  .panel {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 14px;
    .left {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 44px;
      height: 44px;
    }
    .right {
      flex: 1;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-left: 10px;
    }
  }
}
