.aiChat {
  // color: #F3F5FC;
  // background-color: #060607;
  .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 54px;
    height: 54px;
    flex-shrink: 0;
    filter: grayscale(1);
    transition: filter 0.2s;
    .defaultIcon, .hoverIcon {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    .hoverIcon {
      display: none;
    }
  }
  &:hover {
    .icon {
      filter: grayscale(0);
      .defaultIcon {
        display: none;
      }
      .hoverIcon {
        display: block;
      }
    }
  }
  .title {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 4px;
    line-height: 24px;
  }
  .info {
    font-size: 12px;
  }
}
