.container {
  flex: 1;
  .wrap {
    height: 100%;
    max-width: 1080px;
    margin: 0 auto;
    // padding: 40px;
    pointer-events: none;
    display: flex;
    align-items: center;
    .main-panel {
      // 12 * 8
      --app-size: 68px;
      --app-gap-x: 24px;
      --app-gap-y: 24px;
    }
    .workplace-wrap {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .workplace-panel {
      // 16 * 8
      --app-size: 32px;
      --app-gap-x: 10px;
      --app-gap-y: 10px;
      max-width: 664px;
    }
    .grid {
      position: relative;
      user-select: none;
      display: grid;
      width: 100%;
      grid-template-columns: repeat(auto-fill, var(--app-size));
      grid-template-rows: repeat(auto-fill, var(--app-size));
      grid-auto-flow: dense;
      grid-gap: var(--app-gap-y) var(--app-gap-x);
      justify-content: center;
      // padding: 16px 0;
    }
    .item {
      pointer-events: auto;
      position: relative;
      &.size-w-1 {
        grid-column: span 1;
        width: calc(var(--app-size));
      }
      &.size-h-1 {
        grid-row: span 1;
        height: calc(var(--app-size));
      }
      &.size-w-2 {
        grid-column: span 2;
        width: calc(var(--app-size) * 2 + var(--app-gap-y) * 1);
      }
      &.size-h-2 {
        grid-row: span 2;
        height: calc(var(--app-size) * 2 + var(--app-gap-x) * 1);
      }
      &.size-w-3 {
        grid-column: span 3;
        width: calc(var(--app-size) * 3 + var(--app-gap-y) * 2);
      }
      &.size-h-3 {
        grid-row: span 3;
        height: calc(var(--app-size) * 3 + var(--app-gap-x) * 2);
      }
      &.size-w-4 {
        grid-column: span 4;
        width: calc(var(--app-size) * 4 + var(--app-gap-y) * 3);
      }
      &.size-h-4 {
        grid-row: span 4;
        height: calc(var(--app-size) * 4 + var(--app-gap-x) * 3);
      }
      &.size-w-5 {
        grid-column: span 5;
        width: calc(var(--app-size) * 5 + var(--app-gap-y) * 4);
      }
      &.size-h-5 {
        grid-row: span 5;
        height: calc(var(--app-size) * 5 + var(--app-gap-x) * 4);
      }
      &.size-w-6 {
        grid-column: span 6;
        width: calc(var(--app-size) * 6 + var(--app-gap-y) * 5);
      }
      &.size-h-6 {
        grid-row: span 6;
        height: calc(var(--app-size) * 6 + var(--app-gap-x) * 5);
      }
      &.size-w-7 {
        grid-column: span 7;
        width: calc(var(--app-size) * 7 + var(--app-gap-y) * 6);
      }
      &.size-h-7 {
        grid-row: span 7;
        height: calc(var(--app-size) * 7 + var(--app-gap-x) * 6);
      }
      &.size-w-8 {
        grid-column: span 8;
        width: calc(var(--app-size) * 8 + var(--app-gap-y) * 7);
      }
      &.size-h-8 {
        grid-row: span 8;
        height: calc(var(--app-size) * 8 + var(--app-gap-x) * 7);
      }
      &.size-w-9 {
        grid-column: span 9;
        width: calc(var(--app-size) * 9 + var(--app-gap-y) * 8);
      }
      &.size-h-9 {
        grid-row: span 9;
        height: calc(var(--app-size) * 9 + var(--app-gap-x) * 8);
      }
    }
    .subpanel {
      color: var(--system-text-color);
      background-color: var(--system-theme-background-color-hover);
      box-shadow: 0 1px 1px var(--system-theme-border-color);
      border-radius: 16px;
    }
    .panel {
      color: var(--system-text-color);
      background-color: var(--system-theme-background-color);
      box-shadow: rgb(44 52 55 / 0.5) 0px 0px 10px 1px;
      border-radius: 16px;
    }
    .pointer {
      cursor: pointer;
    }
    .hover {
      transition: transform 0.3s;
      &:hover {
        transform: translateY(-1px);
      }
    }
  }
}
