.microPanel {
  width: 100%;
  height: 100%;
  :global {
    .eleven-os-spin-nested-loading {
      height: 100%;
      width: 100%;
      .eleven-os-spin {
        max-height: initial;
      }
    }
    .eleven-os-spin-container {
      height: 100%;
      width: 100%;
      .microAppContainer {
        height: 100%;
        width: 100%;
        & > div {
          height: 100%;
          width: 100%;
        }
      }
    }
  }
}
