.tools {
  // align-items: initial;
  flex-direction: column;
  // justify-content: initial;
  padding: 0;
  .header {
    line-height: 24px;
    box-sizing: border-box;
    text-align: center;
    width: 100%;
    font-size: 12px;
    font-weight: 600;
  }
  .content {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    padding: 0 5px 5px 5px;
    margin-top: -5px;
    .left {
      flex: 2;
      .item {
        height: 100%;
        padding: 5px;
        .wrap {
          font-size: 18px;
          font-weight: 600;
        }
      }
    }
    .right {
      flex: 3;
      border-radius: 16px;
      display: flex;
      flex-wrap: wrap;
      .item {
        width: 50%;
        padding: 5px;
        .wrap {
          font-weight: 600;
          font-size: 14px;
        }
      }
    }
    .wrap {
      background-color: var(--system-theme-background-color-hover);
      box-shadow: 0 1px 1px var(--system-theme-border-color);
      border-radius: 16px;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
