.menu {
  min-width: 148px;
}

.panel {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0px;

  .cover {
    width: 100%;
    height: 200px;
    margin-bottom: 10px;
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.2);
    // backdrop-filter: blur(10px);
  }

  .content {
    width: 100%;
    height: 200px;
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.2);
    // backdrop-filter: blur(10px);
  }

  .avatar {
    width: 180px;
    height: 180px;
    position: absolute;
    left: -70px;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 50%;
    border: 10px solid transparent;
    background-color: rgba(255, 255, 255, 0.2);
    // backdrop-filter: blur(10px);
  }
}

.noShadow {
  :global {
    .eleven-os-modal-content {
      box-shadow: none;
    }
  }
}
