.games {
  padding: 0;
  flex-direction: column;
  .cover {
    width: 100%;
    height: 50px;
    .item {
      height: 50px;
      line-height: 50px;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      .icon {
        width: 24px;
        height: 24px;
        border-radius: 5px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        color: var(--item-color);
        transition: all 0.2s;
        background-color: var(--system-theme-background-color-hover);
        position: relative;
      }
    }
    :global(.slick-current) {
      .item .icon {
        transform: scale(1.3);
      }
    }
  }
  .info {
    width: 100%;
    height: 110px;
    background-color: var(--system-theme-background-color-hover);
    .item {
      height: 110px;
      padding: 12px;
      color: var(--system-text-color);
      cursor: pointer;
      .title {
        font-size: 16px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-weight: 500;
        width: 100%;
      }
      .info {
        height: 40px;
        font-size: 12px;
        line-height: 16px;
        font-weight: 400;
        padding-top: 8px;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        overflow: hidden;
        -webkit-line-clamp: 2;
        opacity: 0.65;
      }
    }
  }
  :global {
    .eleven-os-carousel .slick-list .slick-slide {
      pointer-events: auto;
    }
  }
}
