.taskbar {
  color: var(--system-text-color);
  border-bottom: 1px solid var(--system-theme-border-color);
  background-color: var(--system-theme-background-color);
  // box-shadow: var(--system-theme-border-color) 0px 0px 3px 1px;
  box-shadow: rgb(44 52 55 / 0.5) 0px 0px 10px 1px;
  flex-shrink: 0;
  height: 28px;
  width: 100%;
  padding: 0 8px;
  display: flex;
  align-items: center;
  z-index: 101;
  transition: all .1s ease-out;
}
