
:root {
  // 通用
  --system-theme-color: #13c2c2;
  --system-theme-color-hover: #36cfc9;
  --system-theme-color-active: #08979c;

  // 系统颜色
  --system-theme-border-color: rgb(233 237 240 / 1);
  --system-theme-background-color: rgb(255 255 255 / 1);
  --system-theme-background-color-hover: rgb(239 243 244 / 1); // rgb(246 247 249 / 1);
  --system-theme-background-linear: linear-gradient(180deg, rgba(246, 247, 249, .5), rgba(246, 247, 249, 0) 48.32%);

  // 文本颜色
  --system-text-color: rgb(44 52 55 / 1);
  --system-text-color-hover: rgb(85 96 102 / 1);
}

:root[data-theme="dark"] {
  // 系统颜色
  --system-theme-border-color: rgb(44 52 55 / 1);
  --system-theme-background-color: rgb(13 18 28 / 1);
  --system-theme-background-color-hover: rgb(44 52 55 / 1);
  --system-theme-background-linear: linear-gradient(180deg, rgba(44, 52, 55, .5), rgba(44, 52, 55, 0) 48.32%);

  // 文本颜色
  --system-text-color: rgb(255 255 255 / 1);
  --system-text-color-hover: rgb(233 237 240 / 1);
}
