.warp {
  padding: 0;
}

.nav {
  width: 100%;
  padding: 10px;
  font-size: 11px;
  display: block;
  cursor: default;
  .header {
    display: flex;
    white-space: nowrap;
    .tag {
      cursor: pointer;
      padding: 2px 4px;
      margin-right: 6px;
      border-radius: 3px;
      white-space: nowrap;
      font-weight: 600;
      line-height: 16px;
      &.active {
        background: var(--system-theme-background-color-hover);
      }
    }
  }
  .content {
    height: calc(100% - 28px);
    margin-top: 8px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    scrollbar-width: none;
    .item {
      display: flex;
      line-height: 22px;
      padding: 0 4px;
      &.w50 {
        width: 50%;
      }
      &:hover {
        .title {
          text-decoration: underline;
        }
      }
    }
    .num {
      flex-shrink: 0;
      width: 16px;
      font-weight: 700;
      text-align: center;
      margin-right: 4px;
    }
    .title {
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
  }
}
