.warp {
  padding: 0;
}

.screen {
  width: 100%;
  padding: 0 10px;
  text-align: center;
  .title {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 16px;
  }
  .list {
    display: flex;
    .item {
      padding: 0 4px;
      flex: 1;
      border-right: 1px solid #ccc;
      &:last-child {
        border-right: none;
      }
      .text {
        opacity: 0.5;
        font-size: 12px;
      }
      .value {
        margin-top: 4px;
        font-size: 24px;
        white-space: nowrap;
        font-weight: 500;
      }
    }
  }
}
